const ichingNumber = ({ group }) => {
    if(['A', 'B', 'CC', 'C'].includes(group)) {
        return true;
    }
    return false;
}

const abab = ({ number }) => {
    
    if(number[7] === number[5] && number[4] === number[6]) {
        return true;
    }
    return false
}

const aaa = ({ number }) => {
    const search = [...Array(9).keys()].map(e => `${e}${e}${e}`)
    return !!search.find(e => number.includes(e))
}


const huat = ({ number }) => {
    if(number.includes('888')) {
        return true;
    }

    return false
}

export const filters = {
    All: () => true,
    ['I Ching']: ichingNumber,
    'ABAB': abab,
    'AAA': aaa,
    'Huat': huat
}